/**
 * Parse the uuid from string to the charts data api format
 *
 * Example:
 *
 * Input:
 *  88a0b602d7130100b96d45a3ce61030c:0df47e02d7130100b96d45a3ce610399:03309f95d90010105a9288fde7bd565d:4dfa4c11d7001100ba986f14ce6103e2;4233Dd7001100ba986f14ce6103e2
 *
 * Output:
 *{
 *		"indicator": "88a0b602d7130100b96d45a3ce61030c",
 *		"breakdowns": [
 *			{
 *				"sys_id": "0df47e02d7130100b96d45a3ce610399",
 *				"element_sys_ids": ["03309f95d90010105a9288fde7bd565d"]
 *			}
 *		],
 *		"aggregate": "4dfa4c11d7001100ba986f14ce6103e2",
 *		"domain": "4233Dd7001100ba986f14ce6103e2"
 *}
 *
 * @param {String} uuid - old uuid format
 * @returns {Object}
 */
export const parseUuid = (uuid) => {
	if (!uuid)
		return;

	const domainSplit = uuid.split(';');
	const uuidTokens = domainSplit[0].split(':');

	const hasAggregate = uuidTokens.length % 2 === 0;
	const breakdownTokens = hasAggregate ? uuidTokens.slice(1, uuidTokens.length - 1) : uuidTokens.slice(1, uuidTokens.length);
	const breakdowns = [];

	for(let i = 0; i < breakdownTokens.length; i = i + 2) {
		breakdowns.push({
			sys_id: breakdownTokens[i],
			element_sys_ids: breakdownTokens[i + 1].split(',')
		});
	}

	return {
		indicator: uuidTokens[0],
		breakdowns,
		domain: domainSplit.length === 2 ? domainSplit[1] : null,
		aggregate: hasAggregate ? uuidTokens[uuidTokens.length - 1] : null
	};
};