import '@servicenow/now-chart-sparkline';

export default (state, dispatch) => {
	if (!state.chartData)
		return;

	const options = {
		width: "auto",
		height: 24,
		seriesConfig: {
			spark: { x: "x", y: "data_0_0", type: "area" }
		},
		seriesData: state.chartData ? state.chartData.seriesData : []
	}

	return (
		<div>
			{!state.isLoading ? <now-chart-sparkline {...options} /> : <now-loader size="md" /> }
		</div>
	);
};