// @flow
import { actionTypes } from '@servicenow/ui-core';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { DEFAULT_PAGE_SIZE } from './now-scorecard-list';
import get from 'lodash/get';

export const FETCH_INDICATORS = 'FETCH_INDICATORS';
export const FETCH_INDICATORS_START = 'FETCH_INDICATORS_START';
export const FETCH_INDICATORS_ERROR = 'FETCH_INDICATORS_ERROR';
export const FETCH_INDICATORS_COMPLETED = 'FETCH_INDICATORS_COMPLETED';
export const FETCH_ALL_INDICATORS_WITH_CONDITIONS = 'FETCH_ALL_INDICATORS_WITH_CONDITIONS';
export const FETCH_DISPLAY_INDICATORS_WITH_CONDITIONS = 'FETCH_DISPLAY_INDICATORS_WITH_CONDITIONS';
export const FILTER_INDICATORS = 'FILTER_INDICATORS';
export const FILTER_RESULTS = 'FILTER_RESULTS';
export const FETCH_SORTED_INDICATORS = 'FETCH_SORTED_INDICATORS';
export const UPDATE_PAGE = 'NOW_PAGINATION#PAGE_UPDATE';
export const UPDATE_LIMIT = 'NOW_PAGINATION#LIMIT_UPDATE';
export const NOW_SORTER_CLICKED = 'NOW_SORTER#CLICKED';
export const NOW_BUTTON_CLICKED = 'NOW_BUTTON#CLICKED';
export const FETCH_PARAMETERS_RESET = 'FETCH_PARAMETERS_RESET';
import { t } from 'sn-translate';

const baseApiUrl = 'api/now/v1/par/score_list';

const fetchAction = (coeffects) => {
	const { action: { payload: { sortBy, sortDir, name } } } = coeffects;

	let url = baseApiUrl + '?display_publish_on_scorecards=true&page=:page&per_page=:pageSize';

	if (sortBy) {
		url += `&sort_by=${sortBy}`;
	}
	if (sortDir) {
		url += `&sort_dir=${sortDir}`;
	}
	if (name) {
		url += `&contains=${name}`;
	}

	return createHttpEffect().effect(
		url,
		{
			method: 'GET',
			pathParams: ['page', 'pageSize'],
			batch: true,
			successActionType: FETCH_INDICATORS_COMPLETED,
			startActionType: FETCH_INDICATORS_START,
			errorActionType: FETCH_INDICATORS_ERROR
		},
		coeffects);
};

export const actionHandlers = {
	[actionTypes.COMPONENT_BOOTSTRAPPED]: (coeffects) => {
		const { dispatch, state } = coeffects;
		dispatch(FETCH_INDICATORS, { page: state.page, pageSize: state.pageSize, sortBy: state.sortBy, sortDir: state.sortDir, name: null });
	},
	[FETCH_PARAMETERS_RESET]: ({ dispatch, updateState }) => {
		dispatch(FETCH_INDICATORS, { page: 1, pageSize: DEFAULT_PAGE_SIZE, sortBy: null, sortDir: 'ASC', name: null });
		updateState({
			filterBy: null,
			sortBy: null,
			sortDir: 'ASC'
		});
	},
	[UPDATE_PAGE]: (coeffects) => {
		const { action: { payload: { page }}, state, dispatch, updateState } = coeffects;
		updateState({
			page
		});
		dispatch(FETCH_INDICATORS, { page, pageSize: state.pageSize, sortBy: state.sortBy, sortDir: state.sortDir, name: state.filterBy});
	},
	[UPDATE_LIMIT]: (coeffects) => {
		const { action: { payload: { limit } }, state, dispatch, updateState } = coeffects;

		// if we have less results than the new pageSize we reset to page 1
		var pageNum = state.page;
		if (limit > state.total)
			pageNum = 1;

		updateState({
			pageSize: limit,
			page: pageNum
		});
		dispatch(FETCH_INDICATORS, { page: 1, pageSize: limit, sortBy: state.sortBy, sortDir: state.sortDir, name: state.filterBy });
	},
	[FETCH_INDICATORS]: fetchAction,
	[FILTER_INDICATORS]: (coeffects) => {
		const name = coeffects.properties.name;
		const display = coeffects.properties.onlyPublished;
		if (display) {
			coeffects.dispatch(FETCH_DISPLAY_INDICATORS_WITH_CONDITIONS, {
				page: 1,
				pageSize: limit,
				sortBy: state.sortBy,
				sortDir: state.sortDir,
				contains: state.filterBy,
				name
			});
		} else {
			coeffects.dispatch(FETCH_ALL_INDICATORS_WITH_CONDITIONS, { page: 1,
				pageSize: limit,
				sortBy: state.sortBy,
				sortDir: state.sortDir,
				contains: state.filterBy,
				name });
		}
	},

	[FETCH_ALL_INDICATORS_WITH_CONDITIONS]: createHttpEffect(baseApiUrl + '?contains=:name&display_publish_on_scorecards=all', {
		method: 'GET',
		pathParams: ['name', 'page', 'pageSize', 'sortBy', 'sortDir'],
		batch: true,
		successActionType: FETCH_INDICATORS_COMPLETED
	}),

	[FETCH_DISPLAY_INDICATORS_WITH_CONDITIONS]: createHttpEffect(baseApiUrl + '?contains=:name&display_publish_on_scorecards=true', {
		method: 'GET',
		pathParams: ['name'],
		batch: true,
		successActionType: FETCH_INDICATORS_COMPLETED
	}),

	[FETCH_SORTED_INDICATORS]: createHttpEffect(baseApiUrl + '?display_publish_on_scorecards=true&sort_by=:name&sort_dir=desc', {
		method: 'GET',
		pathParams: ['name','direction'],
		batch: true,
		successActionType: FETCH_INDICATORS_COMPLETED
	}),

	[FETCH_INDICATORS_COMPLETED]: (coeffects) => {
		coeffects.dispatch('NOW_SCORECARD_LIST#FINISHED_LOADING');
		const { metadata, data } = coeffects.action.payload.result;

		coeffects.updateState({
			indicators: JSON.parse(data),
			total: metadata.size
		});
	},

	[NOW_SORTER_CLICKED]: (coeffects) => {
		const { action: { payload }, state, dispatch, updateState } = coeffects;
		dispatch(FETCH_INDICATORS, { page: 1, pageSize: state.pageSize, sortBy: payload.sortBy, sortDir: payload.direction, name: state.filterBy });
		updateState({
			sortBy: payload.sortBy,
			sortDir: payload.direction,
			page: 1
		})
	},

	[FILTER_RESULTS]: (coeffects) => {
		const { action: { payload }, state, dispatch, updateState } = coeffects;
		const encodedFilterValue = encodeURIComponent(payload.value);
		dispatch(FETCH_INDICATORS, { page: 1, pageSize: state.pageSize, sortBy: payload.sortBy, sortDir: payload.direction, name: encodedFilterValue });
		updateState({
			filterBy: encodedFilterValue,
			page: 1
		})
	},
	'NOW_TEXT_LINK#CLICKED': (coeffects) => {
		const { action, dispatch } = coeffects;
		dispatch('ITEM_SELECTED', {
			renderer_type: 'sn-kpi-details-renderer',
			title: t('KPI Details'),
			params: {
				indicatorSysid: get(action, 'payload.uuid.indicator') || '',
				domain: get(action, 'payload.uuid.domain') || '',
			}
		});
	},

};
