// @flow
import { actionTypes } from '@servicenow/ui-core';
import { createHttpEffect } from '@servicenow/ui-effect-http';

export const FETCH_CHART_DATA = 'FETCH_CHART_DATA';
export const FETCH_CHART_DATA_START = 'FETCH_CHART_DATA_START';
export const FETCH_CHART_DATA_COMPLETED = 'FETCH_CHART_DATA_COMPLETED';
export const FETCH_CHART_DATA_ERROR = 'FETCH_CHART_DATA_ERROR';

const loadSparkline = (coeffects) => {
    const { dispatch, state } = coeffects;
	let dataConfigurationsReqBody = {
		data_configurations: [
			{
				source_type: 'analytics',
				uuid: coeffects.properties.uuid
			}
		],
		chart_type: 'area'
	};
	dispatch(FETCH_CHART_DATA, { dataConfigurationsReqBody }, { dataConfigurationsReqBody });
};

export const actionHandlers = {

    [actionTypes.COMPONENT_BOOTSTRAPPED]: loadSparkline,

    [actionTypes.COMPONENT_PROPERTY_CHANGED]: loadSparkline,

    [FETCH_CHART_DATA]: createHttpEffect('api/now/charts/data', {
        method: 'POST',
		dataParam: 'dataConfigurationsReqBody',
        successActionType: FETCH_CHART_DATA_COMPLETED,
        startActionType: FETCH_CHART_DATA_START,
        errorActionType: FETCH_CHART_DATA_ERROR
    }),

    [FETCH_CHART_DATA_COMPLETED]: (coeffects) => {
        coeffects.dispatch('NOW_PA_SPARKLINE#FINISHED_LOADING');
        coeffects.updateState({
            chartData: coeffects.action.payload.result.payload,
            isLoading: false
        });
    },

    [FETCH_CHART_DATA_START]: (coeffects) => {
        coeffects.updateState({
            isLoading: true
        });
    }
};
