import {createCustomElement} from '@seismic/core';
import snabbdom, { Fragment } from '@seismic/snabbdom-renderer';
import { t } from "sn-translate";
import { SN_PAR_POPOVER_OPENED_SET } from '@now-par-components/sn-par-popover';

import './filter-body';

const popoverConfig = {
	positions: [{trigger: 'bottom-start', content: 'top-start' }],
	hideTail: true,
	manageOpened: true,
}
const cardConfig = {
	card: {
		interaction: 'none',
		size: 'lg',
		slot: 'content'
	}
}


const view = (state, { updateState }) => {
	const setTarget = ref => setTimeout(() => updateState({ targetRef: ref }));

	return (
		<Fragment>
			<now-button-iconic
				slot="trigger"
				bare
				icon="ellipsis-v-fill"
				size="md"
				tooltip-content={t(`Filter`)}
				variant="primary"
				hook-insert={({ elm }) => setTarget(elm)}
				hook-destroy={() => setTarget(null)}
			/>
			<sn-par-popover
				targetRef={state.targetRef}
				position={['bottom-start top-start']}
				opened={state.popoverOpened}
				show-arrow={false}
				offset={[0, 0, 0, 0, 0, 0]}>
				<scorecard-filter-body />
			</sn-par-popover>
		</Fragment>
	);
};

createCustomElement('scorecard-filter', {
	renderer: { type: snabbdom },
	view,
	properties: {},
	initialState: {
		targetRef: null
	},
	actionHandlers: {
		'NOW_BUTTON_ICONIC#CLICKED': ({state, updateState}) => {
			updateState({
				popoverOpened: !state.popoverOpened
			});
		},
		'POPOVER_CLOSE': ({ state, updateState }) => {
			updateState({
				popoverOpened: false
			});
		},
		[SN_PAR_POPOVER_OPENED_SET]: ({ state, updateState }) => {
			updateState({
				popoverOpened: false
			});
		}
	}
});
