import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { actionHandlers, FILTER_INDICATORS, FETCH_INDICATORS } from './actions';

import '../now-grid';

import view from './view';
import styles from './styles.scss';

export const DEFAULT_PAGE_SIZE = 10;

createCustomElement('now-scorecard-list', {
	renderer: {type: snabbdom},
	view,
	properties: {
        name: {
			reflect: true,
			onChange(name, previousName, dispatch) {
				if (name)
					dispatch(FILTER_INDICATORS)
				else
					dispatch(FETCH_INDICATORS)
			}
		},
		onlyPublished: {
			reflect: true,
			onChange(onlyPublished, previousOnlyPublished, dispatch) {
				dispatch(FILTER_INDICATORS)
			}
		},
		withScore: {
			reflect: true,
		},
		showMinimized: {
			default: true,
		},
		options: {
			default: {
				minimizedCount: 4
			}
		}
    },
	actionHandlers,
	styles,
	initialState: {
		page: 1,
		pageSize: DEFAULT_PAGE_SIZE,
		total: 10,
		sortBy: 'INDICATOR_GROUP',
		sortDir: 'ASC',
		filterBy: null,
		filterOpen: false
	}
});
