

export default (state) => {

	const filterIndicators = (indicators, withScores) => {
	return withScores && indicators ?
		indicators.filter(indicator => indicator.value != null) :
		indicators;
}

	const paginationProps = {
		limit: state.pageSize,
		page: state.page,
		recordCount: state.total,
		visibleCount: state.total,
		hideLimitSelector: false,
		hideRange: false,
		hideRowCount: false,
		hideLastPage: false,
		hideFirstPage: false,
		hidePages: false
	};

	return (
		<div className="now-scorecard-list">
			<now-grid
				indicators={filterIndicators(
					state.indicators,
					state.properties.withScore,
					state.properties.showMinimized,
					state.properties.options.minimizedCount)}
				pagination-config={paginationProps}
			/>

		</div>
	);
};
