import { createCustomElement } from '@seismic/core';
import snabbdom, { Fragment } from '@seismic/snabbdom-renderer';
import get from 'lodash/get';
import styles from './styles.scss';
import { t } from "sn-translate";
import { FETCH_PARAMETERS_RESET } from '../now-scorecard-list/actions';


const view = (state, { updateState }) => {
	const onChangeInput = (e) => {
		const value = get(e, 'target.value');
		updateState({
			filterValue: value
		})
	};
	return (
		<div className="filter-body" aria-role="dialog" aria-live="assertive">
			<div className="filter-title" aria-label={t('Filter')}>{t('Filter') }</div>
			<div className="filter-input-area">
				<input
					className="sn-el-input"
					value={state.filterValue}
					on-input={onChangeInput}
					aria-label={t('Search field')}>
				</input>
			</div>
			<div className="filter-actions-area">
				<now-button label="Clear" size="sm" variant="secondary" append-to-payload={{id: 'clear'}}></now-button>
				<now-button label="Apply" size="sm" variant="primary" append-to-payload={{id: 'apply', value: state.filterValue}}></now-button>
			</div>
		</div>
	);
};

createCustomElement('scorecard-filter-body', {
	renderer: { type: snabbdom },
	view,
	styles,
	initialState: {
		filterValue: ''
	},
	actionHandlers: {
		'NOW_BUTTON#CLICKED': (coeffects) => {
			const { action: { payload }, dispatch, updateState } = coeffects;
			if (payload.id === 'apply' && payload.value) {
				dispatch('FILTER_RESULTS', { value: payload.value });
			} else if (payload.id === 'clear') {
				updateState({
					filterValue: ''
				});
				dispatch(FETCH_PARAMETERS_RESET, {});
			}
			dispatch('POPOVER_CLOSE');
		},
	}

});
