import { t } from 'sn-translate';
import { parseUuid } from './parse-uuid';

const renderIndicator = (indicator) => {
	return (
		<tr>
			<th scope="row" className="name">
				<now-text-link
					href="javascript:void(0)"
					label={indicator.name}
					aria-label={indicator.name}
					variant="primary"
					append-to-payload={{ uuid: indicator.uuid }}
				/>
			</th>
			<td scope="row" className="trend">
				<now-pa-sparkline uuid={indicator.uuid} />
			</td>
			<td scope="row" className="score">{indicator.value_unit}</td>
			<td scope="row" className="change">{indicator.change_formatted}</td>
			<td scope="row" className="target">{indicator.target_formatted}</td>
		</tr>
	)
}

// We are changing the UUID for the indicators with the required format for the new charts API
export default ({ properties }) => (
	<div className="now-grid">
		<table role="grid" className="sn-list-table" cellspacing="0" cellpadding="0">
			<thead>
				<tr>
					<th scope="col" className="name with-filter">
						<div className="with-filter">
							<now-sorter label={t('Indicators')} sort-by="INDICATOR_GROUP" />
							<span><scorecard-filter></scorecard-filter></span>
						</div>
					</th>
					<th scope="col" className="trend">{t('pa_trend')}</th>
					<th scope="col" className="score"><now-sorter label={t('Score')} sort-by="VALUE" /></th>
					<th scope="col" className="change"><now-sorter label={t('Change')} sort-by="CHANGE" /></th>
					<th scope="col" className="target"><now-sorter label={t('Target')} sort-by="TARGET" /></th>
				</tr>
			</thead>
			<tbody>
			{properties.indicators && properties.indicators.map(indicator => renderIndicator({...indicator, uuid: parseUuid(indicator.uuid.toString())}))}
			</tbody>
		</table>
		<now-pagination {...properties.paginationConfig} />
	</div>
);
