import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './styles.scss';

createCustomElement('now-sorter', {
	renderer: {type: snabbdom},
    view,
    properties: {
		label: {
			default: ''
		},
		id: {
			default: ''
		},
		sortBy: {
			default: ''
		},
		sortActionName: {
			default: 'NOW_SORTER#CLICKED'
		}
	},
	initialState: {
		direction: 'ASC'
	},
	eventHandlers: [{
		events: ['click'],
		effect({ dispatch, state, updateState }) {
			const direction = state.direction === 'ASC' ? 'DESC' : 'ASC';
			updateState({
				direction
			});
			dispatch(state.properties.sortActionName, {
				label: state.properties.label,
				sortBy: state.properties.sortBy,
				direction
			});
		}
	}],
	styles
});
